.container {
  margin-bottom: 40px;
}

.title {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  margin-bottom: 24px;
}

.titleIcon {
  width: 20px;
  height: 20px;
  margin-left: 6px;
}

.values {
  font-weight: 500;
  display: flex;
  flex-direction: column;
}

.label {
  margin-bottom: 8px;
  color: var(--gray-500);
  font-size: 10px;
}

.value {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
